@tailwind base;
@tailwind components;
@tailwind utilities;

/*Importing a Google font*/
@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@300;400;500;600;700;800;900&display=swap');

/*This code applies the font across the website where needed*/
@layer base {
    body {
        @apply font-[Raleway];
    }
    li {
        @apply px-4; /*This gives padding between list items*/
        @apply cursor-pointer /*This gives a cursor pointer for list items*/
    }
}

