/*css custom styling section for work.jsx*/
.content-div {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    height: 250px;
}

.content-div:hover {
    background-image: linear-gradient(
        to right, rgba(112, 157, 255, 0.8),
        hsla(242, 74%, 61%, 0.8)
    )!important ; /*This important tag is here because react was not looking in this file for this styling it was only looking in the work.jsx file*/
}